.squarewall{
    grid-row: 1/2;
    grid-column: 1/2;
    display: grid;
    grid-template-columns: repeat(20, 50px) auto;
    grid-template-rows: repeat(10, 10%);
}
.square{
    background-color: #1e272e;
    height: 100%;
    width: 100%;
    z-index: 100;
    transition: opacity 2s;
}
.empty{
    background-color: none;
}
#empty1{
    grid-row: 1/2;
    grid-column: 16/22;
}
#empty2{
    grid-row: 2/3;
    grid-column: 14/22;
}
